  box-shadow: 2px 2px 2px 2px gray;
  padding: 20px;
  display: flex;
}
.openMd {
  -webkit-animation: OpenM 0.4s ease-out forwards;
          animation: OpenM 0.4s ease-out forwards;
}
@-webkit-keyframes OpenM {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  50% {
    opacity: 1;
    -webkit-transform: translateY(20%);
            transform: translateY(20%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
@keyframes OpenM {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  50% {
    opacity: 1;
    -webkit-transform: translateY(20%);
            transform: translateY(20%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
.closeMd {
  -webkit-animation: CloseM 0.4s ease-out forwards;
          animation: CloseM 0.4s ease-out forwards;
}
@-webkit-keyframes CloseM {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
  50% {
    opacity: 0.8;
    -webkit-transform: translateY(60%);
            transform: translateY(60%);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
}
@keyframes CloseM {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
  50% {
    opacity: 0.8;
    -webkit-transform: translateY(60%);
            transform: translateY(60%);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
}
.openBd {
  display: block;
}
.closeBd {
  display: none;
}
